/**
 *  -0  0
 *
 * @param {number} a 
 * @param {number} b 
 * @returns {boolean} 
 */
export function is (a, b) {
  if (a !== b) {
    return false
  }

  return 1 / a === 1 / b
}

/**
 *  0  1 0  -10  0
 *
 * @param {number} a 
 * @param {number} b 
 * @returns {boolean} 
 */
export function sign (num) {
  if (num === 0) {
    return 0
  }

  return num > 0 ? 1 : -1
}

/**
 * 0 -0 
 * @param {number} num 
 * @returns {boolean} 
 */
export function isPositive (num) {
  return num > 0 || is(num, 0)
}



